import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../environments/environment';
import { Observable, Subject } from 'rxjs';

const config = {
  'serverIP': environment.serverIP,
  'reqHeader': new HttpHeaders({
    'Content-Type': 'application/json',
    'access-control-allow-origin': '*',
    // 'authorization': 'Bearer ' + localStorage.getItem('token') //JWT
  }),
};


interface sd {
  hour: string[];
  id: string[];
  holidays: string[];
}
interface sd_district {
  hour: string[];
  district: number[];
  holidays: string[];
}
interface offstreet {
  start: string;
  end: string;
  id: string[];
  hour: string[];
  holidays: string[];
  revenue: string;
}
interface onstreet {
  start: string;
  end: string;
  id: string[];
  hour: string[];
  holidays: string[];
}
interface total_id {
  sd: number;
  on_street: number;
  off_street: number;
}
interface mutli {
  start: string;
  end: string;
  district: number[];
  hour: string[];
  holidays: string[];
  is_day: string;
  revenue: string;
}
interface mutli_id {
  start: string;
  end: string;
  id: string[];
  hour: string[];
  holidays: string[];
  is_day: string;
  revenue: string;
}
@Injectable({
  providedIn: 'root'
})
export class CenterService {

  constructor(
    private http: HttpClient,
  ) { }

  /*==================================
    Subject
  ==================================*/
  // 
  loading$ = new Subject<boolean>();
  // components
  fillter_sd_id$ = new Subject<sd>();
  fillter_sd_district$ = new Subject<sd_district>();
  fillter_multi$ = new Subject<mutli>();
  fillter_multi_id$ = new Subject<mutli_id>();
  fillter_offStreet$ = new Subject<offstreet>();
  fillter_onStreet$ = new Subject<onstreet>();
  fillter_onStreet_days$ = new Subject<onstreet>();
  // 
  total_id$ = new Subject<total_id>();
  //  -1:, -99:
  mapSelectType$ = new Subject<number>();
  // id
  returnMapSelectId$ = new Subject<string[]>();
  // 
  returnMapSelectParking$ = new Subject<number[]>();
  //
  returnReady$ = new Subject<string>();
  // 1:, 2:
  estimate_btn$ = new Subject<number>();
  // 
  estimate_result$ = new Subject<number[]>();

  //google map 
  address_search$ = new Subject<string>();

  // get
  public get(url, params = {}): Observable<any> {
    const URL = config.serverIP + url;
    return this.http.get<any>(URL, { headers: config.reqHeader, params: params });
  }

  // post
  public post(url, data): Observable<any> {
    const URL = config.serverIP + url;
    return this.http.post<any>(URL, data, { headers: config.reqHeader, withCredentials: true });
  }

}


